import React from 'react';
import DemoBlock from 'docs/mobileComponents/DemoBlock';
import Toast from '../Toast';
import Button from '../../Button';

function showToast() {
  Toast.info('This is a toast tips !!!', 1);
}

function showToastNoMask() {
  Toast.info('Toast without mask !!!', 2, null, false);
}

function successToast() {
  Toast.success('Load success !!!', 1);
}

function failToast() {
  Toast.fail('Load failed !!!', 1);
}

function offline() {
  Toast.offline('Network connection failed !!!', 1);
}

function loadingToast() {
  Toast.loading('Loading...', 1, () => {
    console.log('Load complete !!!');
  });
}

export default class ToastExample extends React.PureComponent {
  render() {
    return (
      <div>
        <DemoBlock title="" className="has-padding">
          <Button inline onClick={showToast}></Button>
          <Button inline onClick={showToastNoMask}>mask</Button>
        </DemoBlock>

        <DemoBlock title="/" className="has-padding">
          <Button inline onClick={successToast}></Button>
          <Button inline onClick={failToast}></Button>
        </DemoBlock>

        <DemoBlock title="" className="has-padding">
          <Button onClick={offline}></Button>
        </DemoBlock>

        <DemoBlock title="loading" className="has-padding">
          <Button onClick={loadingToast}>loading</Button>
        </DemoBlock>
      </div>
    );
  }
}
